.spot {
    -webkit-animation: spot 3s linear infinite;
            animation: spot 3s linear infinite;

}
        
@-webkit-keyframes spot{
    from {
        background: white;
        opacity: 1;
    }
    to {
        background: #e67639;
        -webkit-transform: scale(2,2);
                transform: scale(2,2);
        -webkit-transform: translate(0);
                transform: translate(0);
        opacity: 0;
    }
}
        
@keyframes spot{
    from {
        background: white;
        opacity: 1;
    }
    to {
        background: #e67639;
        -webkit-transform: scale(2,2);
                transform: scale(2,2);
        -webkit-transform: translate(0);
                transform: translate(0);
        opacity: 0;
    }
}

